import React from 'react'
import { Typography, Card, CardContent, CardMedia, Grid } from '@mui/material'

import './classes.css'
//import { Button } from 'react-scroll'

const Classes = () => {
  return (
    <section id="classes">
      <Typography variant="h3" className="title">
        Classes
      </Typography>
      <Card className="class">
        <CardContent>
          <Grid container style={{ display: "flex", alignItems: "center" }}>
            <Grid xs={12} md={6} style={{ padding: "0 0.5rem" }}>
              <div style={{ paddingBottom: "3rem" }}>
                <Typography variant="h4">
                  6:30 AM IST
                </Typography>
                <Typography variant="h4">
                  Yoga for well-being
                </Typography>
              </div>
              <Typography>
                A description of the class here.
              </Typography>
            </Grid>
            <Grid xs={12} md={6} style={{ padding: "0 0.5rem" }}>
              <CardMedia component="video" controls src="video/Mantra chanting.mp4" />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
      <Card className="class">
        <CardContent>
          <Grid container style={{ display: "flex", alignItems: "center" }}>
            <Grid xs={12} md={6} style={{ padding: "0 0.5rem" }}>
              <div style={{ paddingBottom: "3rem" }}>
                <Typography variant="h4">
                  3:30 AM IST
                </Typography>
                <Typography variant="h4">
                  Yoga for International citizens
                </Typography>
              </div>
              <Typography>
                A description of the class here.
              </Typography>
            </Grid>
            <Grid xs={12} md={6} style={{ padding: "0 0.5rem" }}>
              <CardMedia component="video" controls src="video/Naari shuddhi pranayama.mp4" style={{ paddingBottom: "2rem" }} />
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </section>
  )
}

export default Classes