import React from 'react'
import { Typography, Grid } from '@mui/material'

import './footer.css'

const Footer = () => {
  return (
    <section id="footer">
      <Grid container className="holder-container">
        <Grid xs={12} md={4} className="link-holder">
          <img src="images/logo.png" alt="Yogamitali Logo" />
        </Grid>
        <Grid xs={12} md={8} className="link-holder">
          <a className="link" href="https://goo.gl/maps/r53Qi2tAHCVemVPy7" target="blank">
            <i class="fa fa-map-marker fa-2x" aria-hidden="true"></i>
            <div>
              <Typography>
                AC 149, Action Area I,
              </Typography>
              <Typography>
                New Town, West Bengal 700156
              </Typography>
            </div>
          </a>
          <a className="link" href="tel:+919996709419">
            <i class="fa fa-phone fa-2x" aria-hidden="true"></i>
            <Typography>
              +91 99967 09419
            </Typography>
          </a>
          <a className="link" href="mailto:yogawithms@gmail.com">
            <i class="fa fa-envelope fa-2x" aria-hidden="true"></i>
            <Typography>
              yogawithms@gmail.com
            </Typography>
          </a>
          <a className="link" href="https://www.youtube.com/channel/UC9Hh-ujTPpln8eVQc0oVpmw" target="blank">
            <i class="fa fa-youtube fa-2x" aria-hidden="true"></i>
            <Typography>
              Maitriyog
            </Typography>
          </a>
        </Grid>
      </Grid>
      <Typography textAlign="center">&#169; YogaMitali 2022. All Rights Reserved.</Typography>
    </section>
  )
}

export default Footer